* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

:root, body, html {
  min-height: 100vh !important;
  height: auto;
}


body {
  background: -webkit-gradient(linear, left top, right top, from(#A8CABA), to(#5D4157));
  background: -webkit-linear-gradient(left, #A8CABA, #5D4157);
  background: -o-linear-gradient(left, #A8CABA, #5D4157);
  background: linear-gradient(to right, #A8CABA, #5D4157);
}


h1 {
  color: rgba(255, 255, 255, 0.7) !important;
  margin-bottom: 2rem;
}

hr {
  border: 1px solid #cccccc;
}

button {
  color: rgba(255, 255, 255, 0.9) !important;
}
button[disabled] {
  opacity: 0.6;
}

.TodoList-Container {
  max-width: 700px;
  -webkit-transition: height 0.2s ease-out;
  -o-transition: height 0.2s ease-out;
  transition: height 0.2s ease-out;
  padding-right: 3.3rem;
  padding-left: 3.6rem;
  padding-bottom: 1rem;
}

.TodoList-List-Container {
  margin-left: -3.6rem;
  padding-left: 3.6rem;
  margin-right: -3.3rem;
  padding-right: 3.3rem;
  overflow-y: auto;
  overflow-x: hidden;
  max-height: 380px;
}

.TodoList-List-Container::-webkit-scrollbar {
  width: 5px;
} 

.TodoList-List-Container::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.5); 
}

.TodoList-List-Container::-webkit-scrollbar-track {
  background: transparent;
}


.TodoList-Fallback {
  display: block;
  width: 100%;
  text-align: center;
  padding: 1.6rem 1.5rem;
  background: rgba(255, 255, 255, 0.7);
  text-transform: uppercase;
  font-size: 1.1rem;
}


.ListItem-Add {
  margin-top: 1rem;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.ListItem-Add.focused {
  -webkit-transform: perspective(500px) translateZ(-10px);
          transform: perspective(500px) translateZ(-10px);
  -webkit-box-shadow: 0 8px 18px rgba(0,0,0,.16) !important;
          box-shadow: 0 8px 18px rgba(0,0,0,.16) !important;
}


.TodoList-Item {
  margin-bottom: 1rem;
  background: rgba(255, 255, 255, 0.95);
  opacity: 0;
  -webkit-transform: perspective(500px) translateY(20px) translateZ(0);
          transform: perspective(500px) translateY(20px) translateZ(0);
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-font-smoothing: subpixel-antialiased;
  -webkit-transition: opacity 0.2s ease-out, -webkit-transform 0.2s ease-out;
  transition: opacity 0.2s ease-out, -webkit-transform 0.2s ease-out;
  -o-transition: opacity 0.2s ease-out, transform 0.2s ease-out;
  transition: opacity 0.2s ease-out, transform 0.2s ease-out;
  transition: opacity 0.2s ease-out, transform 0.2s ease-out, -webkit-transform 0.2s ease-out;
  will-change: transform;
}

.TodoList-Item.shown {
  opacity: 1;
  -webkit-transform: perspective(300px) translateY(0) translateZ(0);
          transform: perspective(300px) translateY(0) translateZ(0);
}

.TodoList-Item:hover {
  -webkit-transform: perspective(300px) translateZ(5px);
          transform: perspective(300px) translateZ(5px);
}

.TodoList-Item .TodoList-Item-Do {
  -webkit-transform: translate(-10px, -50%);
      -ms-transform: translate(-10px, -50%);
          transform: translate(-10px, -50%);
}

.TodoList-Item .TodoList-Item-Remove {
  -webkit-transform: translate(10px, -50%);
      -ms-transform: translate(10px, -50%);
          transform: translate(10px, -50%);
}

.TodoList-Item.done {
  opacity: 0.7;
  text-decoration: line-through;
  -webkit-transform: perspective(500px) translateZ(-20px);
          transform: perspective(500px) translateZ(-20px);
}

.TodoList-Item .TodoList-Item-Opt {
  width: 36px;
  -webkit-transition: opacity 0.2s ease-out, -webkit-transform 0.2s ease-out;
  transition: opacity 0.2s ease-out, -webkit-transform 0.2s ease-out;
  -o-transition: opacity 0.2s ease-out, transform 0.2s ease-out;
  transition: opacity 0.2s ease-out, transform 0.2s ease-out;
  transition: opacity 0.2s ease-out, transform 0.2s ease-out, -webkit-transform 0.2s ease-out;
  opacity: 0;
  vertical-align: middle;
}

.TodoList-Item:hover .TodoList-Item-Opt {
  opacity: 1;
}

.TodoList-Item-Content {
  display: block;
  padding: 1rem 0.8rem;
  font-size: 1.1rem;
}

.TodoList-Item-Edit {
  font-size: 1.18rem;
  width: 100%;
  padding: 1rem 0.8rem;
}

.TodoList-Info {
  color: rgba(255, 255, 255, 0.9);
  margin-top: 1rem;
  font-size: 1.2rem;
}


